.button_icon {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 0, 0, 0);
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  outline: none;
  text-decoration: none;
}
.button_icon:hover {
  background-color: #a9a9a9;
}
