.dialogs {
  display: flex;
  color: #000;
  flex: 1;
  margin: 0 auto;
  width: 80vw;
}
.none_scrolling {
  height: 100vh;
  overflow: hidden;
}
.messageBox {
  flex: 1;
}
/* .messagesContent {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(32%, 1fr));
  grid-gap: 10px;
} */

.messageInput {
  flex: 1;
  padding: 10px;
}
.input {
  /* border-radius: 0.4rem; */
  min-height: 25px;
  height: fit-content;

  /* border: 1px solid #a2a59f; */
  width: 100%;
}
.input:hover,
input:focus,
input:active {
  outline: none;
}
.input_new_description {
  height: fit-content;
  resize: vertical;
  width: 100%;
  min-height: 60px;
  max-height: 200px;
  margin-top: 15px;
}
.input_new_title {
  max-height: fit-content;
}

/* .checkedBlock {
  grid-column: 2/4;
  display: flex;
  justify-content: center;
} */
.checkedOptions {
  display: flex;
  justify-content: center;
}
.funcBlock {
  justify-content: space-between;
  align-items: center;
  min-height: 50px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.button_send_block {
  display: flex;
  flex: 1;
  flex-direction: row-reverse;
  margin-top: 10px;
}
.button_all_posts {
  margin-right: 5px;
}

/* .search {
  grid-column: 1/1;
  width: 70%;
  height: 25px;
} */

.button_block {
  display: flex;
  justify-content: flex-end;
}

@media (max-width: 599px) {
  .checkedBlock {
    margin-top: 20px !important;
    order: 3;
  }
  .search {
    order: 1;
  }
  .header_button_block {
    order: 2;
  }
}

@media (max-width: 420px) {
  .checked {
    flex-direction: column;
    align-content: flex-end;
    align-items: flex-end;
  }
}
